	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Skipping School","date":"23 Mar 2001","slug":"/2001-03-23-skipping-school/","tags":"Y2001"},"html":"<p>We're home, but not well. Mama and I both have the flu, she quite a bit worse than I, leaving Crhistopher to be healthy, active, and wondering who will be taking care of him. </p>\n<p>It might have something to do with the Minnesota March weather we've been having. Yech!</p>\n<p>Christopher's lip is nearly back to normal. Two small white slits are all that can be seen.</p>"},"next":{"fields":{"title":"Don't Know Its Destination","date":"21 Mar 2001","slug":"/2001-03-21-dont-know-its-destination/"}},"previous":{"fields":{"title":"Gotta walk for air","date":"24 Mar 2001","slug":"/2001-03-24-gotta-walk-for-air/"}}},{"node":{"fields":{"title":"Don't Know Its Destination","date":"21 Mar 2001","slug":"/2001-03-21-dont-know-its-destination/","tags":"Y2001"},"html":"<p><b>Proud to be German?</b><br />\n<a href=\"http://www.faz.com/IN/INtemplates/eFAZ/docmain.asp?rub=&#123;B1311FCC-FBFB-11D2-B228-00105A9CAF88&#125;&doc=&#123;D9E673E0-1D33-11D5-A3B5-009027BA22E4&#125;\">Background</a> from the conservative FAZ (English Edition).  State elections (coming up this weekend) seem to bring out the worst in German politicians, whether it's a petition against dual-citizenship, a 'Kinder staat Inder' (children not Indians) campaign against the green card, or now a silly debate whether it's OK to say that one is proud to be German. It seems especially silly to me, being born in a country where patriotism is, well, as American as apple pie. It's fun listening to CDU politicians whine, though. Helmut Kohl never had such a thin skin. Kohl was never thin, period.</p>\n<p>Ich muß Harald Schmidt zustimmen: lieber stolz, ein Deutscher zu sein, als stolz, Laurenz Meyer zu sein!</p>"},"next":{"fields":{"title":"Madman Situation","date":"20 Mar 2001","slug":"/2001-03-20-madman-situation/"}},"previous":{"fields":{"title":"Skipping School","date":"23 Mar 2001","slug":"/2001-03-23-skipping-school/"}}},{"node":{"fields":{"title":"Madman Situation","date":"20 Mar 2001","slug":"/2001-03-20-madman-situation/","tags":"Y2001"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/03/office.jpg\" height=\"244\" width=\"325\" border=\"0\" alt=\"office.jpg: \" /></p>\n<p>Instead of quoting random songs in my page titles, I think I quote the same <a href=\"http://www.mandymoore4always.org/sugababes/overload.html\">song</a> for a few days, at least until I get tired of it. Americans probably won't recognize it, since the <a href=\"http://www.uk.bol.com/cec/cstage?ecaction=bolprditmview&PrdId=1000002001773781\">Sugababes</a> haven't arrived there yet.</p>\n<p>My employer's semi-relaunch went off without a hitch last night. To roll out the new logo for CeBIT, we basically bolted the agency's new navigation onto the old content. In a couple weeks we'll get it done right. I'm no designer, but to my eye the site looks alright. There's an awful lot of JavaScript, though.</p>\n<p>As new parents, you can find all sorts of timetables and milestones your child should be reaching, and you might even take some of them seriously. Christopher is now 15 months old, an age at which 90% of children can walk. He's in the other 10%. He's almost there, but other than a breathless half-step from the couch to the coffee table, he's not walking. He's big for his age, and his coordination needs to catch up to his size, so we're not at all worried. </p>\n<p>(<a href=\"http://shanson.editthispage.com/discuss/msgReader$537?mode=day\">Al gives</a> a more informed explanation: \"Bigger kids need some major muscle strength in both their legs and abdomen to walk erect. Smaller kids have a lot less mass and shorter moment arm to hold erect.\")</p>\n<p>But then we read that a child can start learning to use a spoon at 8 months. Christopher's only relationship with a spoon so far is to open his mouth and wait. I thought we could give self-feeding a try this evening. He didn't do too badly, but I still have a shirt full of yoghurt to show for it.</p>"},"next":{"fields":{"title":"Overload","date":"19 Mar 2001","slug":"/2001-03-19-overload/"}},"previous":{"fields":{"title":"Don't Know Its Destination","date":"21 Mar 2001","slug":"/2001-03-21-dont-know-its-destination/"}}}],"pathPrefix":"","first":false,"last":false,"index":1013,"pageCount":1101}}

